/* Style the container div */
div {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
  }
  
  /* Style the Greeting component */
  .Greeting {
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 1rem;
    box-shadow: 0 0 1rem rgba(255, 255, 255, 0.1);
    padding: 1rem;
    text-align: center;
    width: 200px;
  }
  
  /* Style the Greeting component text */
  .Greeting p {
    color: rgb(214, 105, 196);
    font-size: 1.5rem;
    margin: 0;
    text-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.5);
  }


  .counter {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 20px;
    background-color: #f2f2f2;
    border: 2px solid #ccc;
    border-radius: 8px;
  }
  
  .count-text {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .button {
    background-color: #4CAF50;
    color: white;
    border: none;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 5px;
    cursor: pointer;
    border-radius: 8px;
    transition: background-color 0.3s ease;
  }
  
  .button:hover {
    background-color: #3e8e41;
  }
  
  .reset-button {
    background-color: #f44336;
  }
  
  .reset-button:hover {
    background-color: #c62828;
  }


  .greeting {
    text-align: center;
    background-color: #f2f2f2;
    padding: 20px;
    border: 2px solid #ccc;
    border-radius: 8px;
  }
  
  h1 {
    font-size: 36px;
    margin: 0;
  }


  .person-list-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #f2f2f2;
    padding: 20px;
    border: 2px solid #ccc;
    border-radius: 8px;
    width: 80%;
    margin: auto;
  }
  
  table {
    border-collapse: collapse;
    width: 100%;
    margin-top: 20px;
  }
  
  th, td {
    padding: 8px;
    text-align: left;
    border-bottom: 1px solid #ddd;
  }
  
  th {
    background-color: #ccc;
  }
  
  .remove-button, .add-button {
    background-color: #4CAF50;
    border: none;
    color: white;
    padding: 10px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 8px;
  }
  
  .remove-button {
    background-color: #f44336;
  }
  
  .form-input {
    padding: 8px;
    border-radius: 8px;
    border: 1px solid #ccc;
    margin-right: 10px;
    margin-bottom: 10px;
    width: 200px;
  }
  
  .form-input:focus {
    outline: none;
    border: 1px solid #4CAF50;
  }